import { UserServiceProvider } from './../providers/user-service/user-service';
import { RegistrationPage } from './../pages/registration/registration';
import { LoginPageModule } from './../pages/login/login.module';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import {crank} from './app.firebaseconfig';
import {AngularFireAuthModule} from 'angularfire2/auth';
import {AngularFireModule} from 'angularfire2';
import { MyApp } from './app.component';
import { AuthProvider } from '../providers/auth/auth';
import { HomePageModule } from '../pages/home/home.module';
import { ListPageModule } from '../pages/list/list.module';
import { NewsfeedPageModule } from '../pages/newsfeed/newsfeed.module';
import { EventsAttendingPageModule } from '../pages/events-attending/events-attending.module';
import { MessagingPageModule } from '../pages/messaging/messaging.module';
import { FriendsPageModule } from '../pages/friends/friends.module';
import { EventDetailsPageModule } from '../pages/event-details/event-details.module';
// import { MyEventsPageModule } from '../pages/my-events/my-events.module';
import {GroupChatPage} from '../pages/group-chat/group-chat';
// import { AddEventPageModule } from '../pages/add-event/add-event.module';
import { MyProfilePageModule } from '../pages/my-profile/my-profile.module';
import {HttpModule} from '@angular/http';

import { GoogleMaps } from '@ionic-native/google-maps';
import { Geolocation } from '@ionic-native/geolocation';

@NgModule({
  declarations: [
    MyApp,
    GroupChatPage,
    
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp,{tabsPlacement: 'top'}, 
{
  links: [
    { loadChildren: '../pages/registration/registration.module#RegistrationPageModule', name: 'RegistrationPage', segment: 'registration', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-profile/my-profile.module#MyProfilePageModule', name: 'MyProfilePage', segment: 'my-profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/chats/chats.module#ChatsPageModule', name: 'ChatsPage', segment: 'chats', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/group-chat/group-chat.module#GroupChatPageModule', name: 'GroupChatPage', segment: 'group-chat', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/event-details/event-details.module#EventDetailsPageModule', name: 'EventDetailsPage', segment: 'event-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newsfeed/newsfeed.module#NewsfeedPageModule', name: 'NewsfeedPage', segment: 'newsfeed', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/events-attending/events-attending.module#EventsAttendingPageModule', name: 'EventsAttendingPage', segment: 'events-attending', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/messaging/messaging.module#MessagingPageModule', name: 'MessagingPage', segment: 'messaging', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/friends/friends.module#FriendsPageModule', name: 'FriendsPage', segment: 'friends', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/add-event/add-event.module#AddEventPageModule', name: 'AddEventPage', segment: 'add-event', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-events/my-events.module#MyEventsPageModule', name: 'MyEventsPage', segment: 'my-events', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/buddies/buddies.module#BuddiesPageModule', name: 'BuddiesPage', segment: 'buddies', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/trackmap/trackmap.module#TrackmapPageModule', name: 'TrackmapPage', segment: 'trackmap', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tracker/tracker.module#TrackerPageModule', name: 'TrackerPage', segment: 'tracker', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireAuthModule,
    LoginPageModule,
    MyProfilePageModule,
    EventDetailsPageModule,
    AngularFireModule.initializeApp(crank)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    GroupChatPage
    
    
  ],
  providers: [
    StatusBar,
    SplashScreen,
    GoogleMaps,
    Geolocation,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    UserServiceProvider
    
  ]
})
export class AppModule {}



